.loginBox{
    text-align: center;
    padding: 30px 0;
    background-color: rgba(190, 213, 222, 0.65);
    border-radius: 10px;
    box-shadow: 18px 14px 17px -1px rgba(32, 51, 64, 0.36);
}

input {
    margin-left: 45px;
    line-height: 35px;
    border: 2px solid transparent;
    border-bottom-color: #777;
    padding: .2rem 0;
    outline: none;
    background-color: transparent;
    color: #0d0c22;
    margin-bottom: 7px;
    transition: .3s cubic-bezier(0.645, 0.045, 0.355, 1);
}

input:focus, input:hover {
    outline: none;
    padding: .2rem 1rem;
    border-radius: 1rem;
    border-color: #7a9cc6;
}

input::placeholder {
    color: #777;
}

input:focus::placeholder {
    opacity: 0;
    transition: opacity .3s;
}
.error{
    color: red;
}
/* From uiverse.io */
.button {
    margin-top: 10px;
    position: relative;
    border: none;
    background: transparent;
    padding: 0;
    cursor: pointer;
    outline-offset: 4px;
    transition: filter 250ms;
    user-select: none;
    touch-action: manipulation;
}

.shadow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    background: hsl(0deg 0% 0% / 0.25);
    will-change: transform;
    transform: translateY(2px);
    transition: transform
    600ms
    cubic-bezier(.3, .7, .4, 1);
}

.edge {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    background: linear-gradient(
            to left,
            hsl(340deg 100% 16%) 0%,
            hsl(340deg 100% 32%) 8%,
            hsl(340deg 100% 32%) 92%,
            hsl(340deg 100% 16%) 100%
    );
}

.frontText {
    display: block;
    position: relative;
    padding: 12px 27px;
    border-radius: 12px;
    font-size: 1.1rem;
    color: white;
    background: hsl(345deg 100% 47%);
    will-change: transform;
    transform: translateY(-4px);
    transition: transform
    600ms
    cubic-bezier(.3, .7, .4, 1);
}

.button:hover {
    filter: brightness(110%);
}

.button:hover .frontText {
    transform: translateY(-6px);
    transition: transform
    250ms
    cubic-bezier(.3, .7, .4, 1.5);
}

.button:active .frontText {
    transform: translateY(-2px);
    transition: transform 34ms;
}

.button:hover .shadow {
    transform: translateY(4px);
    transition: transform
    250ms
    cubic-bezier(.3, .7, .4, 1.5);
}

.button:active .shadow {
    transform: translateY(1px);
    transition: transform 34ms;
}

.button:focus:not(:focus-visible) {
    outline: none;
}